import React, { useContext, useEffect } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Rating from '@mui/material/Rating';
import BookContext from '../context/BookContext';

import EditIcon from '@mui/icons-material/Edit';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

const Booklist = () => {
  const context = useContext(BookContext);
  const { fetchBook, books } = context;
  useEffect(() => {
    //react-hooks/exhaustive-deps
    fetchBook();
  }, []);
  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell align="center">Publisher</TableCell>
              <TableCell align="center">Authors</TableCell>
              <TableCell align="center">Pages</TableCell>
              <TableCell align="center">Price</TableCell>
              <TableCell align="center">Image</TableCell>
              <TableCell align="center">Rating</TableCell>
              <TableCell align="center" colSpan={2}>Action</TableCell>
              <TableCell align="center">Category</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {books.map((row) => (
              <TableRow key={row._id} >
                <TableCell component="th" scope="row">{row.name}</TableCell>
                <TableCell component="th" scope="row">{row.publisher}</TableCell>
                <TableCell component="th" scope="row">{row.authors}</TableCell>
                <TableCell align="center">{row.pages}</TableCell>
                <TableCell align="center" width={100}>&#36; {row.price}</TableCell>
                <TableCell align="center"> <img src={require(`../../../public/upload/${row.image}`)} height={100} width={80} alt=''/></TableCell>
                <TableCell align="center"><Rating name="read-only" value={row.rating} readOnly /></TableCell>
                <TableCell align="center"><EditIcon  color='primary'/></TableCell>
                <TableCell align="center"><DeleteForeverIcon color='error'/></TableCell>
                <TableCell component="th" scope="row">{row.type?row.type:'text'}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  )
}

export default Booklist