import React, { useState, useEffect } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';


const FeedbackList = () => {
    const [user, setUser] = useState([]);
    const fetchuser = async () => {
        const response = await fetch('http://localhost:5000/api/admin/feedquery', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ type: 'feedback' })
        });
        setUser(await response.json());
    }

    useEffect(() => {
        //react-hooks/exhaustive-deps
        fetchuser()
    }, []);
    return (
        <>
            <TableContainer sx={{mt:5}}>
                <Table sx={{ minWidth: 650}} aria-label="simple table">
                    <TableHead>
                        <TableRow sx={{ textTransform: 'uppercase' }}>
                            <TableCell>Name</TableCell>
                            <TableCell>email</TableCell>
                            <TableCell align="center">Feedback</TableCell>
                            <TableCell align="center">Date</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {user.map((row) => (
                            <TableRow key={row._id} sx={{ textTransform: 'uppercase' }}>
                                <TableCell>{row.fullname}</TableCell>
                                <TableCell>{row.email}</TableCell>
                                <TableCell align="center">{row.message}</TableCell>
                                <TableCell align="center"> {new Date(row.date).toDateString()}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}

export default FeedbackList