import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
//icons
import DashboardIcon from '@mui/icons-material/Dashboard';
import FeedbackIcon from '@mui/icons-material/Feedback';
import QuestionAnswerIcon from '@mui/icons-material/QuestionAnswer';
import BadgeIcon from '@mui/icons-material/Badge';
import ProductionQuantityLimitsIcon from '@mui/icons-material/ProductionQuantityLimits';
import InventoryIcon from '@mui/icons-material/Inventory';
import FactCheckIcon from '@mui/icons-material/FactCheck';
import SellIcon from '@mui/icons-material/Sell';

//import list
import Dash from './Pages/Dash';
import Addproduct from './Pages/Addproduct';
import Booklist from './Pages/Booklist';
import UserList from './List/UserList';
import CartList from './List/CartList';
import SellList from './List/SellList';
import FeedbackList from './List/FeedbackList';
import QueryList from './List/QueryList';
import HelpList from './List/HelpList';


function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div>
            {value === index && (
                <Box className='border'>
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};


export default function Dashboard() {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ display: 'flex' }} >
            <Grid container>
                <Grid xs={6} md={2} className='border' item>
                    <Tabs orientation="vertical" value={value} onChange={handleChange}>
                        <Tab icon={<DashboardIcon />} iconPosition="start" label="Dashboard" sx={{ paddingLeft: 2, paddingRight: 10 }} />
                        <Tab icon={<BadgeIcon />} iconPosition="start" label="Customers" sx={{ paddingLeft: 2, paddingRight: 10 }} />
                        <Tab icon={<ProductionQuantityLimitsIcon />} iconPosition="start" label="Cart List" sx={{ paddingLeft: 0, paddingRight: 10 }} />
                        <Tab icon={<SellIcon />} iconPosition="start" label="Sell List" sx={{ paddingLeft: 0, paddingRight: 10 }} />
                        <Tab icon={<InventoryIcon />} iconPosition="start" label="Add Product" sx={{ paddingLeft: 4, paddingRight: 10 }} />
                        <Tab icon={<FactCheckIcon />} iconPosition="start" label="Product List" sx={{ paddingLeft: 4, paddingRight: 10 }} />
                        <Tab icon={<FeedbackIcon />} iconPosition="start" label="Feedback" sx={{ paddingLeft: 0, paddingRight: 10 }} />
                        <Tab icon={<QuestionAnswerIcon />} iconPosition="start" label="Query" sx={{ paddingLeft: 0, paddingRight: 13 }} />
                        <Tab icon={<QuestionAnswerIcon />} iconPosition="start" label="Help" sx={{ paddingLeft: 0, paddingRight: 13,mb:30 }} />
                    </Tabs>
                </Grid>
                <Grid xs={6} md={10} className='border' item>
                    <TabPanel value={value} index={0}>
                        <Dash />
                    </TabPanel>
                    <TabPanel value={value} index={1}>
                        <UserList/>
                    </TabPanel>
                    <TabPanel value={value} index={2}>
                        <CartList/>
                    </TabPanel>
                    <TabPanel value={value} index={3}>
                        <SellList/>
                    </TabPanel>
                    <TabPanel value={value} index={4}>
                        <Addproduct/>
                    </TabPanel>
                    <TabPanel value={value} index={5}>
                        <Booklist/>
                    </TabPanel>
                    <TabPanel value={value} index={6}>
                        <FeedbackList/>
                    </TabPanel>
                    <TabPanel value={value} index={7}>
                        <QueryList/>
                    </TabPanel>
                    <TabPanel value={value} index={8}>
                        <HelpList/>
                    </TabPanel>
                </Grid>
            </Grid>
        </Box>
    );
}
