import React, { useState, useEffect } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';


const UserList = () => {
    const [user, setUser] = useState([]);
    const fetchuser = async () => {
        const response = await fetch('http://localhost:5000/api/admin/userlist', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
        });
        setUser(await response.json());
    }

    useEffect(() => {
        //react-hooks/exhaustive-deps
        fetchuser()
    }, []);
    return (
        <>
            <TableContainer sx={{mt:5}}>
                <Table sx={{ minWidth: 650}} aria-label="simple table">
                    <TableHead>
                        <TableRow sx={{ textTransform: 'uppercase' }}>
                            <TableCell>Name</TableCell>
                            <TableCell>email</TableCell>
                            <TableCell align="center">gender</TableCell>
                            <TableCell align="center">status</TableCell>
                            <TableCell align="center">photo</TableCell>
                            <TableCell align="center">date</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {user.map((row) => (
                            <TableRow key={row._id} sx={{ textTransform: 'uppercase' }}>
                                <TableCell>{row.name}</TableCell>
                                <TableCell>{row.email}</TableCell>
                                <TableCell align="center">{row.gender}</TableCell>
                                <TableCell align="center">{row.status}</TableCell>
                                <TableCell align="center"><img src={require(`../../../public/upload/${row.img_URL}`)} height={50} width={50} alt='' /></TableCell>
                                <TableCell align="center"> {new Date(row.date).toDateString()}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}

export default UserList