import React, { useState, useEffect } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import CheckBoxOutlineBlankIcon from '@mui/icons-material/CheckBoxOutlineBlank';
import Paper from '@mui/material/Paper';
import CardContent from '@mui/material/CardContent'

const CartList = () => {
    const [user, setUser] = useState([]);
    const fetchuser = async () => {
        const response = await fetch('http://localhost:5000/api/admin/waitlist', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ type: 'wait' })
        });
        setUser(await response.json());
    }

    useEffect(() => {
        //react-hooks/exhaustive-deps
        fetchuser()
    }, []);


    const handleUpdate = async (id, quantity, name) => {
        const cartupdate = await fetch('http://localhost:5000/api/admin/update', {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ id: id })
        });
        setUser(await cartupdate.json());

        const bookupdate = await fetch('http://localhost:5000/api/admin/updatebook', {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ quantity: quantity - 1, name: name })
        });
        setUser(await bookupdate.json());

        fetchuser()
    }

    return (
        <>
            {
                user.length > 0 ?
                    <>
                        <TableContainer>
                            <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                <TableHead sx={{ textTransform: "uppercase" }}>
                                    <TableRow>
                                        <TableCell>ITEMS</TableCell>
                                        <TableCell align="center">PAGES</TableCell>
                                        <TableCell align="center">QUANTITY</TableCell>
                                        <TableCell align="center">PRICE</TableCell>
                                        <TableCell align="center">TOTAL</TableCell>
                                        <TableCell align="center">STATUS</TableCell>
                                        <TableCell align="center">approval</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {user.map((data) => (
                                        <TableRow key={data._id} >
                                            <TableCell align="left" className={data.CartDetails.quantity<2?'border-danger': data.CartDetails.quantity>10?'border-primary':'border-warning'}>
                                                <Paper sx={{ display: 'flex', boxShadow: 0 }}>
                                                    <CardMedia component="img" sx={{ width: 90, height: 140 }} image={require(`../../../public/upload/${data.CartDetails.image}`)}
                                                        alt={data.CartDetails.name} />
                                                    <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
                                                        <CardContent sx={{ flex: '1 0 auto' }}>
                                                            <Typography component="div" color="text.secondary">
                                                                {data.CartDetails.name}
                                                            </Typography>
                                                            <Typography variant="subtitle1" color="text.secondary" component="div" >
                                                                {data.CartDetails.authors}
                                                            </Typography>
                                                        </CardContent>
                                                    </Box>
                                                </Paper>
                                            </TableCell>
                                            <TableCell align="center">{data.CartDetails.pages}</TableCell>
                                            <TableCell align="center">1</TableCell>
                                            <TableCell align="center">{data.CartDetails.price}</TableCell>
                                            <TableCell align="center">{data.CartDetails.price}</TableCell>
                                            {
                                                data.CartDetails.quantity < 0
                                                    ?
                                                    <TableCell align="center"  color='primary' sx={{ textTransform: "uppercase",color:'primary' }}>{data.CartDetails.quantity} {data.CartDetails.status}</TableCell>
                                                    :
                                                    <TableCell align="center" color='error' sx={{ textTransform: "uppercase",color:'error' }} className={data.CartDetails.quantity<2?'text-danger': data.CartDetails.quantity>10?'text-dark':'text-warning'}>{data.CartDetails.quantity} {data.CartDetails.status}</TableCell>
                                            }

                                            <TableCell align="center" onClick={() => { handleUpdate(data._id, data.CartDetails.quantity, data.CartDetails.name) }}><CheckBoxOutlineBlankIcon /></TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </>
                    : ''
            }
        </>
    )
}

export default CartList