import React, { useEffect, useState } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import EditIcon from '@mui/icons-material/Edit';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import ClearIcon from '@mui/icons-material/Clear';
import TextField from '@mui/material/TextField';



const QueryList = () => {

    const [open, setOpen] = useState(false);
    const [show, setdata] = useState();

    //Handle Dialog Box
    const handleDialog = async (id) => {
        const response = await fetch("http://localhost:5000/api/admin/displayquery", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ id: id })
        });
        setdata(await response.json());
        setOpen(true);

    }

    const handleClose = (e) => {
        e.preventDefault();
        setOpen(false);
    }

    const [Answer, setAnswer] = useState();


    const handleChangeAnswer = (event) => {
        setAnswer(event.target.value);
    };

    //Handle Update
    const handleUpdate = async (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const UpdateData = {
            Answer: data.get('Answer'),
            userid: data.get('userid')
        }

        const response = await fetch("http://localhost:5000/api/admin/updateanswer", {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ id: UpdateData.userid, Answer: UpdateData.Answer })
        });
        const json = await response.json();
        fetchuser()
    }



    const [user, setUser] = useState([]);
    const fetchuser = async () => {
        const response = await fetch('http://localhost:5000/api/admin/feedquery', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ type: 'query' })
        });
        setUser(await response.json());
    }

    useEffect(() => {
        //react-hooks/exhaustive-deps
        fetchuser()
    }, []);


    return (
        <>
            <TableContainer sx={{ mt: 5 }}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead>
                        <TableRow sx={{ textTransform: 'uppercase' }}>
                            <TableCell>Name</TableCell>
                            <TableCell>email</TableCell>
                            <TableCell align="center">Query</TableCell>
                            <TableCell align="center">Anwer</TableCell>
                            <TableCell align="center">Date</TableCell>
                            <TableCell align="center">Edit</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {user.map((row) => (
                            <TableRow key={row._id} sx={{ textTransform: 'uppercase' }}>
                                <TableCell>{row.fullname}</TableCell>
                                <TableCell>{row.email}</TableCell>
                                <TableCell align="center">{row.message}</TableCell>
                                <TableCell align="center">{row.Answer}</TableCell>
                                <TableCell align="center"> {new Date(row.date).toDateString()}</TableCell>
                                <TableCell align="center" onClick={() => (handleDialog(row._id))}><EditIcon color='primary' /></TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

            {/* //First Dialog */}
            {
                show ?
                    <Box>
                        <Dialog open={open} component="form" id='update-form' onSubmit={handleUpdate}>
                            <DialogTitle id="alert-dialog-title">
                                <Box className='offset-10'>
                                    <Button onClick={handleClose} ><ClearIcon /></Button>
                                </Box>
                            </DialogTitle>
                            <DialogContent>
                                <TextField fullWidth type='text' name='userid' label="id" value={show[0]._id} variant="standard" />
                                <Grid container spacing={2} sx={{ mb: 2, mt: 2 }}>

                                    <Grid item xs={6}>
                                        <Grid>
                                            <TextField fullWidth type='text' name='username' label="Name" value={show[0].fullname} variant="outlined" disabled />
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Grid>
                                            <TextField fullWidth type='email' name='email' label="Email" value={show[0].email} variant="outlined" disabled />
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <TextField fullWidth type='text' name='Message' label="Message" value={show[0].message} variant="outlined" multiline rows={5} disabled />
                                <TextField fullWidth sx={{ mt: 2 }} type='text' name='Answer' label="Answer" value={Answer} multiline rows={5} onChange={handleChangeAnswer} variant="outlined" required />
                            </DialogContent>
                            <DialogActions>
                                <Button type='submit' fullWidth variant="contained">Update</Button>
                            </DialogActions>
                        </Dialog>
                    </Box>
                    : ''
            }
        </>
    )
}

export default QueryList