import React, { useContext } from 'react'
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import BookContext from '../context/BookContext';

import InputLabel from '@mui/material/InputLabel';
import NativeSelect from '@mui/material/NativeSelect';

const Addproduct = () => {
    const context = useContext(BookContext);
    const { addBook } = context;

    const handleSubmit = (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const actualData = {
            name: data.get('Name'),
            title: data.get('title'),
            authors: data.get('author'),
            publisher: data.get('publisher'),
            pages: data.get('pages'),
            rating: data.get('rating'),
            desc: data.get('desc'),
            price: data.get('price'),
            keyword: data.get('keyword'),
            type: data.get('type'),
            quantity: data.get('quantity'),
            image: data.get('image').name,
            status: data.get('status'),
            variants: data.get('Variants'),
            AudioLink: data.get('AudioLink'),
        }
        addBook(actualData);
        document.getElementById('upload-form').reset();
    }

    return (
        <>
            <Box component="form" autoComplete="off" onSubmit={handleSubmit} action="/addbooks" encType='multipart/form-data' id='upload-form'>
                <Grid container spacing={2}>
                    <Grid item md={5} sx={{ m: 2 }}>
                        <div>
                            <TextField
                                required
                                fullWidth
                                label="Name"
                                name='Name'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="Title"
                                name='title'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="author"
                                name='author'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="publisher"
                                name='publisher'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="Pages"
                                name='pages'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="Rating"
                                name='rating'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="Price"
                                name='price'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="Quantity"
                                name='quantity'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="Status"
                                name='status'
                                defaultValue='available'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />
                            <TextField
                                required
                                fullWidth
                                label="keyword"
                                name='keyword'
                                variant="standard"
                                sx={{ mt: 2 }}
                            />

                        </div>
                    </Grid>
                    <Grid item md={6} sx={{ m: 2 }}>

                        <InputLabel variant="standard" htmlFor="uncontrolled-native" >
                            Category
                        </InputLabel>
                        <NativeSelect fullWidth name='type'>
                            <option value='Disney'>Disney</option>
                            <option value='Child'>Child</option>
                            <option value='Mystery'>Mystery</option>
                            <option value='Thrillers'>Thrillers</option>
                            <option value='Crime'>Crime</option>
                            <option value='Poetry'>Poetry</option>
                            <option value='Horror'>Horror</option>
                            <option value='Science Fiction'>Science Fiction</option>
                            <option value='Comics &#38; Graphic Novels'>Comics &#38; Graphic Novels</option>
                            <option value='Literature &#38; Fiction'>Literature &#38; Fiction</option>
                            <option value='Manga'>Manga</option>
                            <option value='Historical Fiction'>Historical Fiction</option>
                            <option value='Fantasy'>Fantasy</option>
                            <option value='LGBTQ+ Fiction'>LGBTQ+ Fiction</option>
                            <option value='Romance'>Romance</option>
                            <option value='History'>History</option>
                            <option value='Spirituality &#38; Religion'>Spirituality &#38; Religion</option>
                            <option value='Business &#38; Investing'>Business &#38; Investing</option>
                            <option value='Self Development &#38; Hobbies'>Self Development &#38; Hobbies</option>
                            <option value='Science &#38; Technology'>Science &#38; Technology</option>
                            <option value='Travel'>Travel</option>
                            <option value='Cooking &#38; Wine'>Cooking &#38; Wine</option>
                            <option value='Humor &#38; Games'>Humor &#38; Games</option>
                            <option value='Arts &#38; Photography'>Arts &#38; Photography</option>
                            <option value='Biography &#38; Memoir'>Biography &#38; Memoir</option>
                            <option value='LGBTQ+ Nonfiction'>LGBTQ+ Nonfiction</option>
                            <option value='Audio books'>Audio books</option>
                        </NativeSelect>

                        <TextField fullWidth placeholder='Enter Description'
                            margin='normal' variant='standard'
                            label='Description'
                            name='desc'
                            multiline rows={6}
                            helperText='minimum 144 characters'
                        />
                        <TextField
                            fullWidth
                            type="date"
                            name='date'
                            variant="standard"
                            sx={{ mt: 2 }}
                        />
                        <TextField
                            type="file"
                            name='image'
                            sx={{ mt: 5 }}
                        />
                        <InputLabel variant="standard" htmlFor="uncontrolled-native" sx={{ mt: 6 }} >
                            Variants
                        </InputLabel>
                        <NativeSelect fullWidth name='Variants'>
                            <option value='text'>Text</option>
                            <option value='audio'>Audio</option>
                        </NativeSelect>
                        <TextField
                            required
                            fullWidth
                            label="AudioLink"
                            name='AudioLink'
                            variant="standard"
                            sx={{ mt: 2 }}
                        />
                    </Grid>
                    <div>
                        <Button variant="contained" color="secondary" type='submit' sx={{ ml: 4 }}>Submit</Button>
                        <Button variant="contained" color="error" type='reset' sx={{ m: 2 }}>Reset</Button>
                    </div>
                </Grid>
            </Box>
        </>
    )
}

export default Addproduct