import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import HomeChart from "../Chat/HomeChart"
import HomeLinechart from '../Chat/HomeLinechart';

//icons
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import SupervisedUserCircleIcon from '@mui/icons-material/SupervisedUserCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
const Dash = () => {

    const [wait, setWait] = useState([]);
    const [earn, setEarn] = useState([]);
    const [Buy, setBuy] = useState([]);
    const [user, setUser] = useState([]);

    const fetchuser = async () => {
        const countwait = await fetch('http://localhost:5000/api/admin/cartcount', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ type: 'wait' })
        });
        setWait(await countwait.json());

        const countbuy = await fetch('http://localhost:5000/api/admin/cartcount', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ type: 'Buy' })
        });
        setBuy(await countbuy.json());

        
        const EarnCount = await fetch('http://localhost:5000/api/admin/earncount', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
        });
        setEarn(await EarnCount.json());


        const usercount = await fetch('http://localhost:5000/api/admin/usercount', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
        });
        setUser(await usercount.json());
    }

    useEffect(() => {
        //react-hooks/exhaustive-deps
        fetchuser()
    }, []);
    let a = 0;
    earn.map((data) => (
      a = data.CartDetails.price + a
    ))

    return (
        <>
            <Box sx={{ flexGrow: 1, mt: 2 }}>
                <Grid container spacing={2}>
                    <Grid item xs={6} md={3}>
                        <Paper elevation={3} sx={{ textAlign: 'center', backgroundColor: '#f73378', height: 200, pt: 8, }}>
                            <h2>{a}<sup>+</sup></h2>
                            <p><MonetizationOnIcon fontSize="small" />&nbsp;Total Earn</p>
                        </Paper>
                    </Grid>
                    <Grid item xs={6} md={3}>
                        <Paper elevation={3} sx={{ textAlign: 'center', backgroundColor: '#5393ff', height: 200, pt: 8 }}>
                            <h2>{user}<sup>+</sup></h2>
                            <p><SupervisedUserCircleIcon fontSize="small" />&nbsp;Total Customers</p>
                        </Paper>
                    </Grid>
                    <Grid item xs={6} md={3}>
                        <Paper elevation={3} sx={{ textAlign: 'center', backgroundColor: '#33eb91', height: 200, pt: 8 }}>
                            <h2>{wait}<sup>+</sup></h2>
                            <p><ShoppingCartIcon fontSize="small" />&nbsp;Total Cart Request</p>
                        </Paper>
                    </Grid>
                    <Grid item xs={6} md={3}>
                        <Paper elevation={3} sx={{ textAlign: 'center', backgroundColor: '#ffcf33', height: 200, pt: 8 }}>
                            <h2>{Buy}<sup>+</sup></h2>
                            <p><LocalShippingIcon fontSize="small" />&nbsp;Total Delivery</p>
                        </Paper>
                    </Grid>
                </Grid>
                <Grid container spacing={2} sx={{ mt: 2 }}>
                    <Grid item xs={6} md={6}>
                        <Paper elevation={3} >
                            <HomeChart />
                        </Paper>
                    </Grid>

                    <Grid item xs={6} md={6}>
                        <Paper elevation={3}>
                            <HomeLinechart />
                        </Paper>
                    </Grid>
                </Grid>
            </Box>
        </>
    )
}

export default Dash

