import React, { useState, useEffect } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import CheckBoxIcon from '@mui/icons-material/CheckBox';
import Paper from '@mui/material/Paper';
import CardContent from '@mui/material/CardContent'

const SellList = () => {
    const [user, setUser] = useState([]);
    const fetchuser = async () => {
        const response = await fetch('http://localhost:5000/api/admin/waitlist', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ type: 'Buy' })
        });
        setUser(await response.json());
    }
    console.log(user)

    useEffect(() => {
        //react-hooks/exhaustive-deps
        fetchuser()
    }, []);
    return (
        <>
            <TableContainer>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead sx={{ textTransform: "uppercase" }}>
                        <TableRow>
                            <TableCell>ITEMS</TableCell>
                            <TableCell align="center">PAGES</TableCell>
                            <TableCell align="center">QUANTITY</TableCell>
                            <TableCell align="center">PRICE</TableCell>
                            <TableCell align="center">TOTAL</TableCell>
                            <TableCell align="center">STATUS</TableCell>
                            <TableCell align="center">approval</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {user.map((data) => (
                            <TableRow key={data._id}>
                                <TableCell align="left">
                                    <Paper sx={{ display: 'flex', boxShadow: 0 }}>
                                        <CardMedia component="img" sx={{ width: 90, height: 140 }} image={require(`../../../public/upload/${data.CartDetails.image}`)}
                                            alt={data.CartDetails.name} />
                                        <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
                                            <CardContent sx={{ flex: '1 0 auto' }}>
                                                <Typography component="div">
                                                    {data.CartDetails.name}
                                                </Typography>
                                                <Typography variant="subtitle1" color="text.secondary" component="div">
                                                    {data.CartDetails.authors}
                                                </Typography>
                                            </CardContent>
                                        </Box>
                                    </Paper>
                                </TableCell>
                                <TableCell align="center">{data.CartDetails.pages}</TableCell>
                                <TableCell align="center">1</TableCell>
                                <TableCell align="center">{data.CartDetails.price}</TableCell>
                                <TableCell align="center">{data.CartDetails.price}</TableCell>
                                <TableCell align="center" sx={{ textTransform: "uppercase" }}>{data.CartDetails.quantity} {data.CartDetails.status}</TableCell>
                                <TableCell align="center"><CheckBoxIcon /> <br /> <br />{new Date().toDateString(data.date)}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}

export default SellList